.box-body {
  padding: 0 10%;
}

.circle {
  -webkit-border-radius: 60px;
  -moz-border-radius: 60px;
  -ms-border-radius: 60px;
  border-radius: 60px;
  margin: 0 auto;
  height: 100px;
  width: 100px;
  text-align: center;
}

.header {
  padding-top: 130px;
  padding-bottom: 10px;
  text-align: center;
}

.navbar {
  background: #ffffff;
  letter-spacing: 0.5px;
  padding: 10px 0;
  border: none;
  border-bottom: 2px solid #00BCD4;
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  -ms-border-radius: 0;
  border-radius: 0;
  -webkit-box-shadow: 0px 3px 5px 0px rgba(10, 39, 60, 0.11);
  box-shadow: 0px 3px 5px 0px rgba(10, 39, 60, 0.11);
}

.navbar .anchorTheme, .navbar .navbar-nav > li > a:hover, .navbar .navbar-nav > li > a:focus, .navbar .navbar-brand:focus, .navbar .navbar-brand:hover {
  color: #ec5447;
  background-color: transparent;
}

.navbar .fa {
  color: #ec5447;
}

.navbar .navbar-nav > li > a {
  color: #00BCD4;
}

.navbar .navbar-brand {
  color: #00BCD4;
}

.navbar .navbar-brand > img:hover {
  -webkit-animation: rotateIn 1s forwards;
  -moz-animation: rotateIn 1s forwards;
  -ms-animation: rotateIn 1s forwards;
  -o-animation: rotateIn 1s forwards;
  animation: rotateIn 1s forwards;
}

html,
body {
  height: 100%;
}

body {
  font-family: 'AlrightSans',Helvetica,'Roboto',Arial,sans-serif;
  color: #0a273c;
  font-style: normal;
  background-image: url(../../assets/img/crossword.png);
  background-color: #f9f9f9;
}

h1, h2, h3 {
  color: #0a273c;
  font-family: 'Tiempos',Georgia,sans-serif;
  font-weight: 300;
}

p,
li,
li a {
  color: #0a273c;
}

a {
  color: #17bac7;
}

a,
a:focus,
a:hover,
a:active {
  outline: 0 !important;
}

a:focus {
  text-decoration: none;
}

a:hover {
  color: white;
  text-decoration: underline;
}

/* Default highlight color */
::-moz-selection {
  color: #fff;
  background: #ddd;
}

::selection {
  color: #fff;
  background: #ddd;
}

.btn-color, .btn-primary:active, .btn-primary:focus, .btn-primary:hover, .btn-primary:visited, .btn-primary:focus:active, .btn-primary:hover:active, .btn-primary:hover:focus, .btn-primary:visited:active, .btn-primary:visited:hover, .btn-primary:visited:focus {
  background-color: #d44034;
  border-color: #c74034;
}

.btn-primary {
  color: #fff;
  background-color: #ec5447;
  border-color: #c74034;
  outline: 0 !important;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.btn-primary:active, .btn-primary:focus, .btn-primary:hover, .btn-primary:visited {
  -webkit-box-shadow: 0 0 5px 2px rgba(244, 67, 54, 0.5);
  box-shadow: 0 0 5px 2px rgba(244, 67, 54, 0.5);
}

section.login form .box-body, section.login form .box-footer, section.register form .box-body, section.register form .box-footer {
  padding: 0 22%;
}

section.login form .box-body, section.register form .box-body {
  position: relative;
}

section.login form .box-footer a, section.register form .box-footer a {
  color: #17bac7;
  font-size: 11px;
  text-decoration: none;
}

section.login form .form-group, section.register form .form-group {
  margin: 25px 0;
}

section.login form .form-control, section.register form .form-control {
  -webkit-box-shadow: none;
  box-shadow: none;
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  -ms-border-radius: 0;
  border-radius: 0;
  border: none;
  border-bottom: 1px solid #ddd;
  background: inherit;
  padding: 0;
}

section.login form .form-control:focus, section.register form .form-control:focus {
  -webkit-box-shadow: none;
  box-shadow: none;
  border-color: #112d41;
}

section.login .fingerprint, section.register .fingerprint {
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  border-radius: 50%;
  width: 50%;
  height: auto;
  padding-top: 50%;
  background-color: #0a273c;
  background: url(../img/testing_3.png);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  margin: 5% 0 0 25%;
}

section.login .overlay, section.register .overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  padding: 0 20%;
  opacity: 0;
  z-index: -1;
}

section.login .btn-primary, section.register .btn-primary {
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  -ms-border-radius: 0;
  border-radius: 0;
  width: 100%;
  font-size: 20px;
  padding: 15px;
}

.fadeOut {
  -webkit-animation: fadeOut 2s forwards;
  -moz-animation: fadeOut 2s forwards;
  -ms-animation: fadeOut 2s forwards;
  -o-animation: fadeOut 2s forwards;
  animation: fadeOut 2s forwards;
}

.fadeIn {
  -webkit-animation: fadeIn 2s forwards;
  -moz-animation: fadeIn 2s forwards;
  -ms-animation: fadeIn 2s forwards;
  -o-animation: fadeIn 2s forwards;
  animation: fadeIn 2s forwards;
  z-index: 10 !important;
}

/**********************************
|***** Redstamp Customizations ****
|*********************************/
#th {
  font-family: 'TiemposHeadline';
  font-weight: 600;
}

#tt {
  font-family: 'TiemposText';
}

#as {
  font-family: 'AlrightSans';
  font-weight: 600;
  font-size: 18px;
}

span {
  color: #85939e;
}

.rs-container {
  padding: 35px 100px 0;
}

.rs-row {
  margin-top: 10%;
}

.rs-row:after {
  display: block;
  content: "";
  clear: both;
}

.rs-col {
  position: relative;
  min-height: 1px;
  float: left;
  width: calc(100% / 2);
}

.rs-header {
  text-align: left;
  letter-spacing: -0.4px;
}

.rs-header > h1 {
  font-family: 'TiemposText';
  font-size: 44px;
}

.rs-sub-heading {
  text-align: center;
  margin: 7% 0;
}

.rs-text {
  height: 34px;
  font-family: 'AlrightSans';
  font-size: 14px;
  line-height: 1.21;
  text-align: left;
  color: #85939e;
}

.rs-navbar .rs-nav-header {
  float: left;
}

.rs-navbar .rs-nav {
  list-style: none;
  float: right;
  padding: 0;
}

.rs-navbar .rs-nav > li {
  float: left;
}

.rs-navbar .rs-nav > li > a {
  color: #ec5447;
  text-decoration: none;
}

.rs-navbar .rs-nav > li > a:hover {
  color: #00BCD4;
}

.rs-clearfix::after {
  display: block;
  content: "";
  clear: both;
}

/**********************************
|********* Rectangle  *************
|*********************************/
.rectangle {
  background: white;
  margin: 5% 20%;
  box-shadow: 0 0 10px 9px rgba(0, 0, 0, 0.19);
  /**********************************
    |********* Rectangle Top **********
    |*********************************/
  /**********************************
    |********* Rectangle Mid **********
    |*********************************/
  /**********************************
    |********* Rectangle Bot **********
    |*********************************/
}

.rectangle h4 {
  margin: 0 0 10px;
  height: 13px;
  font-family: AlrightSans;
  font-size: 11px;
  font-weight: bold;
  color: #60cbe2;
}

.rectangle h3 {
  margin-top: 0;
}

.rectangle p {
  color: #85939e;
}

.rectangle a:hover {
  color: #60cbe2;
}

.rectangle .col-md-2, .rectangle .col-sm-2 {
  padding: 0;
}

.rectangle .rectangle-top .rs-navbar .rs-nav {
  margin: 8px 0 0;
}

.rectangle .rectangle-mid {
  background-color: #f7f8f9;
  padding-bottom: 10px;
}

.rectangle .rectangle-mid .rs-row {
  padding: 10% 5%;
  margin: 0;
  background: white;
  margin-bottom: 5%;
}

.rectangle .rectangle-mid .rs-container {
  padding-top: 0;
}

.rectangle .rectangle-mid .rs-col {
  width: 25%;
}

.rectangle .rectangle-mid hr {
  margin-top: 15%;
  width: 64px;
  height: 1px;
  opacity: 0.47;
  background-color: #85939e;
}

.rectangle .rectangle-mid .tips {
  border: 1px solid #eee;
}

.rectangle .rectangle-mid .tips .col-md-2, .rectangle .rectangle-mid .tips .col-sm-2 {
  margin-left: -5px;
  margin-right: 5px;
}

.rectangle .rectangle-mid .rs-row.tips {
  padding: 6% 5%;
}

.rectangle .rectangle-bot {
  background-color: #f7f8f9;
  border-top: 1px solid #ddd;
}

.rectangle .rectangle-bot h3 {
  font-size: 16px;
}

.rectangle .rectangle-bot .rs-container {
  padding: 0 100px;
}

.rectangle .rectangle-bot .panel {
  border: 1px solid #eee;
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  -ms-border-radius: 0;
  border-radius: 0;
}

.rectangle .rectangle-bot .panel .panel-body {
  padding: 15px;
}

.rectangle .rectangle-footer .footer-top {
  text-align: center;
  padding: 3%;
  background-color: #ec5447;
}

.rectangle .rectangle-footer .footer-top p {
  color: #e6e6e6;
  margin: 0;
}

.rectangle .rectangle-footer .footer-top a {
  color: #fff;
}

.rectangle .rectangle-footer .footer-mid {
  border-bottom: 1px solid #eee;
}

.rectangle .rectangle-footer .footer-mid .rs-container {
  padding: 15px 100px;
}

.rectangle .rectangle-footer .footer-mid .rs-navbar .rs-nav-header {
  margin-top: 8px;
}

.rectangle .rectangle-footer .footer-mid .rs-navbar .rs-nav > ul {
  margin-bottom: 0;
}

.rectangle .rectangle-footer .footer-mid .rs-navbar .rs-nav > li {
  text-align: center;
  background: #f1f1f1;
  border-radius: 60px;
  height: 35px;
  width: 35px;
  margin: 0 5px;
  cursor: pointer;
}

.rectangle .rectangle-footer .footer-mid .rs-navbar .rs-nav > li:hover .fa {
  color: #ec5447;
}

.rectangle .rectangle-footer .footer-mid .fa {
  line-height: 2.5;
  font-size: 15px;
  color: white;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.rectangle .rectangle-footer .footer-bot .footer-header {
  padding: 0 5% 8%;
}

.rectangle .rectangle-footer .footer-bot a {
  font-family: AlrightSans;
  letter-spacing: 0.5px;
  color: #0a273c;
  text-decoration: none;
}

.rectangle .rectangle-footer .footer-bot a:hover {
  color: #ec5447;
}

@media screen and (min-width: 992px) {
  .rectangle .rectangle-footer .footer-bot .footer-header {
    padding: 0 20% 8%;
  }
}
